import * as React from "react";
import PropTypes from "prop-types";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import CssBaseline from "@mui/material/CssBaseline";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import MenuIcon from "@mui/icons-material/Menu";
import HomeIcon from "@mui/icons-material/Home";
import { Link } from "react-router-dom";
import useMediaQuery from "@mui/material/useMediaQuery";
import Appimage from '../images/appbar.jpg';

const drawerWidth = 240;

function DrawerAppBar(props) {
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const isMobile = useMediaQuery('(max-width:600px)');

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <Box sx={{ textAlign: "center", pt: 2 }}>
      <Typography variant="h6" sx={{ fontFamily: "Poppins, sans-serif", color: "black" }}>
        Creative Hub
      </Typography>
      <Box mt={3}>
        <Link to="/" style={{ textDecoration: 'none', color: 'black' }}>
          <IconButton color="inherit" onClick={handleDrawerToggle}>
            <HomeIcon sx={{ color: 'black' }} />
            <Typography variant="subtitle1" sx={{ marginLeft: 1, color: 'black' }}>
              Home
            </Typography>
          </IconButton>
        </Link>
      </Box>
    </Box>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar position="fixed" sx={{ 
        backgroundColor: "black", 
        marginBottom: "0px", 
        paddingTop: "15px", 
        backgroundImage: `url(${Appimage})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        height: "120px",
        display: "flex", 
      }}>
        <Toolbar sx={{ minHeight: "64px", marginBottom: "0px" }}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: "none" } }}
          >
            <MenuIcon />
          </IconButton>
        
          <Typography
            variant="h4"
            component="div"
            sx={{
              flexGrow: 1,
              display: "flex",
              justifyContent: isMobile ? "center" : "left",
              alignItems: "center",
              fontFamily: "Poppins, sans-serif",
              fontSize: "45px",
              color: "black",
              marginLeft: isMobile ? "0px" : "100px"
            }}
          >
            Creative Hub
          </Typography>
          {!isMobile && (
            <Link to="/" style={{ textDecoration: 'none', color: 'Black', marginLeft: '20px' }}>
              <IconButton color="inherit">
                <HomeIcon />
                <Typography variant="subtitle1" sx={{ marginLeft: 1, fontSize: "25px" }}>
                  Home
                </Typography>
              </IconButton>
            </Link>
          )}
        </Toolbar>
      </AppBar>
      <nav>
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth,
              backgroundColor: "white",
            },
          }}
        >
          {drawer}
        </Drawer>
      </nav>
      <Box component="main" sx={{ p: 3, flexGrow: 1 }}>
        <Toolbar sx={{ minHeight: "64px", marginBottom: "0px" }} />
        {/* Your main content goes here */}
      </Box>
    </Box>
  );
}

DrawerAppBar.propTypes = {
  window: PropTypes.func,
};

export default DrawerAppBar;
