// ContactForm.js

import React from 'react';
import { Box, Typography, TextField, Button } from '@mui/material';
import emailjs from 'emailjs-com';

function ContactForm() {
    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('service_afk99bo', 'template_thhidlo', e.target, 'TlO6RhEnQI0Bf43Nd')
            .then((result) => {
                console.log('Email successfully sent!', result.text);
                // Optionally, you can reset the form after successful submission
                e.target.reset();
            }, (error) => {
                console.error('Failed to send email. Error:', error.text);
            });
    }

    return (
        <Box sx={{ textAlign: 'center' }}>
            <Typography variant="h5" sx={{ fontFamily: 'poppins' }}>Contact Us</Typography>
            <form onSubmit={sendEmail}>
                <TextField
                    required
                    fullWidth
                    name="user_name"
                    label="Your Name"
                    margin="normal"
                />
                <TextField
                    required
                    fullWidth
                    name="user_email"
                    type="email"
                    label="Your Email"
                    margin="normal"
                />
                <TextField
                    required
                    fullWidth
                    multiline
                    rows={4}
                    name="message"
                    label="Your Message"
                    margin="normal"
                />
                <Button type="submit" variant="contained" color="primary">
                    Send Message
                </Button>
            </form>
        </Box>
    );
}

export default ContactForm;
