import React, { useState } from "react";
import Appbar from "./components/Appbar";
import Footer from "./components/Footer";
import img1 from "./images/img02.jpg";
import img2 from "./images/img03.jpg";
import img3 from "./images/img04.jpg";
import img4 from "./images/img19.jpg";
import img5 from "./images/img20.jpg";
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardMedia,
  Modal,
  Backdrop,
  Fade,
} from "@mui/material";

export default function Theatre() {
  const images = [
    { id: 1, src: img1, alt: "Theatre image 1" },
    { id: 2, src: img2, alt: "Theatre image 2" },
    { id: 3, src: img3, alt: "Theatre image 3" },
    { id: 4, src: img4, alt: "Theatre image 4" },
    { id: 5, src: img5, alt: "Theatre image 5" },
  ];

  const [open, setOpen] = useState(false);
  const [currentImage, setCurrentImage] = useState(null);

  const handleOpen = (image) => {
    setCurrentImage(image);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Appbar />
      <Box sx={{ textAlign: "center", mt: 4 }}>
        <Typography sx={{ fontFamily: "poppins" }} variant="h5">
          Theatre
        </Typography>
        <Typography variant="h6" sx={{ fontFamily: "poppins", mt: 2 }}>
          Experience the magic of the performing arts with my theatre projects,
          designed to captivate audiences and provide an immersive entertainment
          experience. Every element is crafted to enhance acoustics, visibility,
          and overall comfort.
        </Typography>
      </Box>
      <Container sx={{ mt: 4 }}>
        <Grid container spacing={4} justifyContent="center">
          {images.map((image) => (
            <Grid item xs={12} sm={6} md={4} key={image.id}>
              <Card onClick={() => handleOpen(image)} style={{borderRadius:"10px"}} elevation={6}>
                <CardMedia
                  component="img"
                  src={image.src}
                  alt={image.alt}
                  style={{
                    objectFit: "cover",
                    width: "100%",
                    height: "250px",
                    cursor: "pointer",
                  }}
                  className="img-hover"
                />
              </Card>
            </Grid>
          ))}
        </Grid>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500,
          }}
        >
          <Fade in={open}>
            <Box
              sx={{
                position: "absolute",
                top: "50%",
                left: "50%",
                transform: "translate(-50%, -50%)",
                maxWidth: "100vw",
                maxHeight: "100vh",
                overflow: "hidden",
                bgcolor: "background.paper",
                boxShadow: 24,
                outline: "none",
              }}
            >
              <img
                src={currentImage ? currentImage.src : ""}
                alt={currentImage ? currentImage.alt : ""}
                style={{
                  objectFit: "contain",
                  width: "100%",
                  height: "100%",
                }}
              />
            </Box>
          </Fade>
        </Modal>
      </Container>

      <Footer />
    </div>
  );
}
