import React from "react";
import { Box, Container, Typography, Grid, Link } from "@mui/material";
import FacebookIcon from "@mui/icons-material/Facebook";
import TwitterIcon from "@mui/icons-material/Twitter";
import InstagramIcon from "@mui/icons-material/Instagram";

export default function Footer() {
  return (
    <Box
      sx={{
        width: "100%",
        backgroundColor: "#000", // Set background color to black
        padding: "20px 0",
        marginTop: "20px",
      }}
    >
      <Container>
        <Grid container spacing={4} justifyContent="space-around">
          <Grid item xs={12} md={4}>
            <Typography variant="h6" gutterBottom sx={{ color: "#fff" }}>
              Contact
            </Typography>
            <Typography sx={{ fontFamily: "Poppins", color: "#fff" }}>
              Creative Hub
            </Typography>

            <Typography sx={{ fontFamily: "Poppins", color: "#fff" }}>
             Location:  No 1140 Lig 2 TNHB Colony<br></br>Velachery<br></br>Chennai-42
            </Typography>
            <Typography sx={{ fontFamily: "Poppins", color: "#fff" }}>
              Email: creativehub.archi@gmail.com
            </Typography>
            <Typography sx={{ fontFamily: "Poppins", color: "#fff" }}>
              Phone: +91 82206 70016
            </Typography>
          </Grid>
          <Grid item xs={12} md={4}>
            <Typography
              sx={{ fontFamily: "Poppins", color: "#fff" }}
              variant="h6"
              gutterBottom
            >
              Follow Us
            </Typography>
            <Box sx={{ display: "flex", gap: "10px" }}>
              <Link href="https://facebook.com" target="_blank" color="inherit">
                <FacebookIcon sx={{ color: "#fff" }} />
              </Link>
              <Link href="https://twitter.com" target="_blank" color="inherit">
                <TwitterIcon sx={{ color: "#fff" }} />
              </Link>
              <Link
                href="https://instagram.com"
                target="_blank"
                color="inherit"
              >
                <InstagramIcon sx={{ color: "#fff" }} />
              </Link>
            </Box>
          </Grid>
          <Grid item xs={12} md={4}>
            <Typography
              sx={{ fontFamily: "Poppins", color: "#fff" }}
              variant="h6"
              gutterBottom
            >
              About
            </Typography>
            <Typography sx={{ fontFamily: "Poppins", color: "#fff" }}>
              Creative Hub is dedicated to transforming spaces into beautiful,
              functional environments that reflect your unique style.
            </Typography>
          </Grid>
        </Grid>
        <Box
          sx={{
            textAlign: "center",
            marginTop: "20px",
          }}
        >
          <Typography
            sx={{ fontFamily: "Poppins", color: "#fff" }}
            variant="body2"
            color="textSecondary"
          >
            &copy; {new Date().getFullYear()} Creative Hub. All rights reserved.
          </Typography>
        </Box>
      </Container>
    </Box>
    
  );
}
