import React, { useState } from "react";
import Appbar from "./components/Appbar";
import Footer from "./components/Footer";
import img1 from "./images/villa1.jpg";
import img2 from "./images/villa2.jpg";
import img3 from "./images/villa3.jpg";
import img4 from "./images/villa4.jpg";
import img5 from "./images/villa5.jpg";
import img6 from "./images/villa6.jpg";
import img7 from "./images/villa7.jpg";
import img8 from "./images/villa8.jpg";
import img9 from "./images/villa9.jpg";
import img10 from "./images/villa10.jpg";
import img11 from "./images/villa11.jpg";
import img12 from "./images/villa12.jpg";
import img13 from "./images/villa13.jpg";
import img14 from "./images/villa 14.jpg";
import img15 from "./images/villa15.jpg";
import img16 from "./images/villa16.jpg";
import img17 from "./images/villa17.jpg";

import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardMedia,
  Modal,
  Backdrop,
  Fade,
} from "@mui/material";

export default function Villa() {
    const images = [
        { id: 1, src: img1, alt: "Villa image 1" },
        { id: 2, src: img2, alt: "Villa  image 2" },
        { id: 3, src: img3, alt: "Villa image 3" },
        { id: 4, src: img4, alt: "Villa image 4" },
        { id: 5, src: img5, alt: "Villa image 5" },
        { id: 6, src: img6, alt: "Villa image 6" },
        { id: 7, src: img7, alt: "Villa image 7" },
        { id: 8, src: img8, alt: "Villa image 8" },

        { id: 10, src: img10, alt: "Villa image 10" },
        { id: 11, src: img11, alt: "Villa image 11" },
        { id: 12, src: img12, alt: "Villa image 12" },
        { id: 13, src: img13, alt: "Villa image 13" },
        { id: 14, src: img14, alt: "Villa image 14" },
        { id: 15, src: img15, alt: "Villa image 15" },
        { id: 16, src: img16, alt: "Villa image 16" },
        { id: 17, src: img17, alt: "Villa image 17" },


      ];
    
      const [open, setOpen] = useState(false);
      const [currentImage, setCurrentImage] = useState(null);
    
      const handleOpen = (image) => {
        setCurrentImage(image);
        setOpen(true);
      };
    
      const handleClose = () => {
        setOpen(false);
      };
    
      return (
        <div>
          <Appbar />
          <Box sx={{ textAlign: "center", mt: 4 }}>
            <Typography sx={{ fontFamily: "poppins" }} variant="h5">
           Villas
            </Typography>
            <Typography variant="h6" sx={{ fontFamily: "poppins", mt: 2 }}>
            Experience luxury redefined in a haven designed for comfort and elegance
            </Typography>
          </Box>
          <Container sx={{ mt: 4 }}>
            <Grid container spacing={4} justifyContent="left">
              {images.map((image) => (
                <Grid item xs={12} sm={6} md={4} key={image.id}>
                  <Card onClick={() => handleOpen(image)} style={{borderRadius:"10px"}} elevation={6}>
                    <CardMedia
                      component="img"
                      src={image.src}
                      alt={image.alt}
                      style={{
                        objectFit: "cover",
                        width: "100%",
                        height: "250px",
                        cursor: "pointer",
                      }}
                      className="img-hover"
                    />
                  </Card>
                </Grid>
              ))}
            </Grid>
            <Modal
              open={open}
              onClose={handleClose}
              aria-labelledby="modal-modal-title"
              aria-describedby="modal-modal-description"
              closeAfterTransition
              BackdropComponent={Backdrop}
              BackdropProps={{
                timeout: 500,
              }}
            >
              <Fade in={open}>
                <Box
                  sx={{
                    position: "absolute",
                    top: "50%",
                    left: "50%",
                    transform: "translate(-50%, -50%)",
                    maxWidth: "100vw",
                    maxHeight: "100vh",
                    overflow: "hidden",
                    bgcolor: "background.paper",
                    boxShadow: 24,
                    outline: "none",
                  }}
                >
                  <img
                    src={currentImage ? currentImage.src : ""}
                    alt={currentImage ? currentImage.alt : ""}
                    style={{
                      objectFit: "contain",
                      width: "100%",
                      height: "100%",
                    }}
                  />
                </Box>
              </Fade>
            </Modal>
          </Container>
    
          <Footer />
        </div>
      );
}
