import React from "react";
import Appbar from "./components/Appbar";
import {
  Container,
  Grid,
  Typography,
  Box,
  Card,
  CardHeader,
  Avatar,

} from "@mui/material";
import { Link } from "react-router-dom";
import img1 from "./images/res9.jpg";
import img2 from "./images/img04.jpg";
import img6 from "./images/hotel1.jpg";
import img0 from "./images/homeImage.jpg";
import shop from "./images/shop1.jpg";
import Footer from "./components/Footer";
import emi from "./images/emi.png";
import warranty from "./images/war.avif";
import Email from "./components/Email";
import villa from "./images/villa1.jpg";
import guarantee from "./images/guarantee.png";
import cust from "./images/cust.png";
import des from "./images/design.avif";
import cons from "./images/consult.avif";
import bee from "./images/beelittle.png";
import waff from "./images/wowfills.jpg";
import cream from "./images/cream.avif";
import kg from "./images/kg.jpeg";
import vbj from "./images/vbj.jpeg";
import sp from "./images/sp.png";
import bb from "./images/bb.avif";
export default function Home() {
  return (
    <div>
      <Appbar />

      <Box
        sx={{
          backgroundImage: `url(${img0})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          height: "500px",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          textAlign: "center",
        }}
      >
        <Typography variant="h3" color="white" sx={{ fontFamily: "cursive" }}>
           Creative Hub Interior Design<br></br> And <br></br>Projects
        </Typography>
      </Box>

      <br />

      <Container>
        <Box sx={{ textAlign: "center" }}>
          <Typography sx={{ fontFamily: "poppins", fontSize: "17px" }}>
            "Welcome to our showcase of diverse projects, including restaurants,
            theatres, shops, salons, and villas. Each project exemplifies our
            commitment to excellence, creativity, and client satisfaction.
            Explore our innovative designs that transform ordinary spaces into
            extraordinary experiences."
          </Typography>
        </Box>
        <br />

        <Box sx={{ textAlign: "center" }}>
          <Typography sx={{ fontFamily: "poppins" }} variant="h5">
            Our Projects
          </Typography>
        </Box>
        <br />

        <Grid container spacing={4} justifyContent="center">
          <Grid item xs={12} sm={6} md={4}>
            <Link to="/restaurant" style={{ textDecoration: "none" }}>
              <Card
                sx={{ padding: "5px", borderRadius: "10px" }}
                elevation={6}
                className="img-hover"
              >
                <img
                  src={img1}
                  alt="img1"
                  width="100%"
                  style={{ borderRadius: "10px" }}
                />
                <br />
                <Box sx={{ justifyContent: "center", textAlign: "center" }}>
                  <Typography sx={{ fontFamily: "poppins" }}>
                    Restaurants
                    <br />
                    Elegant dining experience
                  </Typography>
                </Box>
              </Card>
            </Link>
          </Grid>
          <Grid item xs={12} sm={6} md={4}>
            <Link to="/theatre" style={{ textDecoration: "none" }}>
              <Card
                sx={{ padding: "5px", borderRadius: "10px" }}
                elevation={6}
                className="img-hover"
              >
                <img
                  src={img2}
                  alt="img1"
                  width="100%"
                  style={{ borderRadius: "10px" }}
                />
                <br />
                <Box sx={{ justifyContent: "center", textAlign: "center" }}>
                  <Typography sx={{ fontFamily: "poppins" }}>
                    Theatres
                    <br />
                    Immersive viewing spaces
                  </Typography>
                </Box>
              </Card>
            </Link>
          </Grid>
          <Grid item xs={12} sm={6} md={4}>
            <Link to="/shop" style={{ textDecoration: "none" }}>
              <Card
                sx={{ padding: "5px", borderRadius: "10px" }}
                elevation={6}
                className="img-hover"
              >
                <img
                  src={shop}
                  alt="img1"
                  width="100%"
                  height="300px"
                  style={{ borderRadius: "10px" }}
                />
                <br />
                <Box sx={{ justifyContent: "center", textAlign: "center" }}>
                  <Typography sx={{ fontFamily: "poppins" }}>
                    Shops
                    <br />
                    Modern retail solutions
                  </Typography>
                </Box>
              </Card>
            </Link>
          </Grid>

          <Grid item xs={12} sm={6} md={4}>
            <Link to="/villa" style={{ textDecoration: "none" }}>
              <Card
                sx={{ padding: "5px", borderRadius: "10px" }}
                elevation={6}
                className="img-hover"
              >
                <img
                  src={villa}
                  alt="villa 05"
                  height="300px"
                  width="100%"
                  
                  style={{ borderRadius: "10px" }}
                />
                <Box sx={{ justifyContent: "center", textAlign: "center" }}>
                  <Typography sx={{ fontFamily: "poppins" }}>
                    Villas
                    <br />
                    Luxurious living spaces
                  </Typography>
                </Box>
              </Card>
            </Link>
          </Grid>
          <Grid item xs={12} sm={6} md={4}>
            <Link to="/hotel" style={{ textDecoration: "none" }}>
              <Card
                sx={{ padding: "5px", borderRadius: "10px" }}
                elevation={6}
                className="img-hover"
              >
                <img
                  src={img6}
                  alt="img1"
                  width="100%"
                  height="260px"
                  style={{ borderRadius: "10px" }}
                />
                <br />
                <Box sx={{ justifyContent: "center", textAlign: "center" }}>
                  <Typography sx={{ fontFamily: "poppins" }}>
                    Hotels
                    <br />
                    Comfortable and stylish accommodations
                  </Typography>
                </Box>
              </Card>
            </Link>
          </Grid>
        </Grid>
        <br />
<br></br>
        <Box sx={{ textAlign: "center" }}>
          <Typography sx={{ fontFamily: "poppins" }} variant="h5">
            About us
          </Typography>
          <br />
          <Typography sx={{ fontFamily: "poppins" }}>
            Our Creative Hub is an interior design Company with over 13 years of
            existence in transforming residential and commercial spaces. With a
            keen eye for detail and a passion for design, I have successfully
            completed over 100 projects, each tailored to the individual needs
            and tastes of my clients.
          </Typography>
        </Box>
        <br></br>
        <br />
        <Box sx={{ textAlign: "center" }}>
          <Typography sx={{fontFamily:"poppins"}} variant="h5">
            Client Who Trust us
          </Typography>
        </Box>
        <br></br>
        <Grid container spacing={5} justifyContent="center">
          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{ maxWidth: 345 }} elevation={6}>
              <CardHeader
                avatar={
                  <Avatar alt="R" src={bee}>
                    BL
                  </Avatar>
                }
                title={
                  <Typography style={{ fontFamily: "poppins" }} variant="h5">
                    {" "}
                    Bee-little
                  </Typography>
                }
              />
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{ maxWidth: 345 }} elevation={6}>
              <CardHeader
                avatar={
                  <Avatar alt="R" src={vbj}>
                    VBJ
                  </Avatar>
                }
                title={
                  <Typography style={{ fontFamily: "poppins" }} variant="h5">
                    {" "}
                    vummudi Bangaru
                  </Typography>
                }
              />
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{ maxWidth: 345 }} elevation={6}>
              <CardHeader
                avatar={
                  <Avatar alt="R" src={kg}>
                    KG
                  </Avatar>
                }
                title={
                  <Typography style={{ fontFamily: "poppins" }} variant="h5">
                    {" "}
                    Kg Cinemas
                  </Typography>
                }
              />
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{ maxWidth: 345 }} elevation={6}>
              <CardHeader
                avatar={
                  <Avatar alt="R" src={cream}>
                    CC
                  </Avatar>
                }
                title={
                  <Typography style={{ fontFamily: "poppins" }} variant="h5">
                    {" "}
                    Cream en Creamy
                  </Typography>
                }
              />
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{ maxWidth: 345 }} elevation={6}>
              <CardHeader
                avatar={
                  <Avatar alt="R" src={sp}>
                    SP
                  </Avatar>
                }
                title={
                  <Typography style={{ fontFamily: "poppins" }} variant="h5">
                    {" "}
                    Singapore Plaza
                  </Typography>
                }
              />
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{ maxWidth: 345 }} elevation={6}>
              <CardHeader
                avatar={
                  <Avatar alt="R" src={waff}>
                    WF
                  </Avatar>
                }
                title={
                  <Typography style={{ fontFamily: "poppins" }} variant="h5">
                    {" "}
                    Wow Fills
                  </Typography>
                }
              />
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{ maxWidth: 345 }} elevation={6}>
              <CardHeader
                avatar={
                  <Avatar alt="R" src={bb}>
                    BB
                  </Avatar>
                }
                title={
                  <Typography style={{ fontFamily: "poppins" }} variant="h5">
                    {" "}
                    Big Boss
                  </Typography>
                }
              />
            </Card>
          </Grid>
        </Grid>
        <br></br>
        <br></br>
        <Box>
          <Typography
            sx={{ fontFamily: "poppins", textAlign: "center" }}
            variant="h5"
          >
            Why us?
          </Typography>
        </Box>
        <br />

        <Grid
          container
          spacing={3}
          justifyContent="center"
          className="floating-cards"
        >
          <Grid item xs={12} sm={6} md={3}>
            <Card elevation={5} className="floating-card">
              <div className="card-content">
                <img src={des} alt="Designs" className="card-image" />
                <Typography variant="body1" className="card-text">
                  Free Cost of Designs
                </Typography>
              </div>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card elevation={5} className="floating-card">
              <div className="card-content">
                <img src={guarantee} alt="Guarantee" className="card-image" />
                <Typography variant="body1" className="card-text">
                  45-Days Move-in guarantee
                </Typography>
              </div>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card elevation={5} className="floating-card">
              <div className="card-content">
                <img src={emi} alt="EMI Options" className="card-image" />
                <Typography variant="body1" className="card-text">
                  Easy EMI Options
                </Typography>
              </div>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card elevation={5} className="floating-card">
              <div className="card-content">
                <img src={cust} alt="Happy Customers" className="card-image" />
                <Typography variant="body1" className="card-text">
                  100+ Happy Customers
                </Typography>
              </div>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card elevation={5} className="floating-card">
              <div className="card-content">
                <img src={warranty} alt="Warranty" className="card-image" />
                <Typography variant="body1" className="card-text">
                  5 years warranty
                </Typography>
              </div>
            </Card>
          </Grid>
        </Grid>
        <br />
        <br></br>
       
        <br></br>
      </Container>
      <Box
        sx={{
          backgroundImage: `url(${cons})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
        }}
      >
        <Box
          sx={{
            textAlign: "center",
            backgroundImage: `url(${cons})`,
            backgroundSize: "cover",
            backgroundPosition: "center",
            padding: "30px",
          }}
        >
          <Typography
            variant="h4"
            sx={{ fontFamily: "poppins", color: "white" }}
          >
            Feel free to use the mailbox for any consultancy needs you may have.
          </Typography>
          <br></br>
          <Grid container spacing={4} justifyContent="center">
            <Grid item xs={12} sm={8} md={6}>
              <Card sx={{ padding: "20px" }}>
                <Email />
              </Card>
            </Grid>
          </Grid>
        </Box>
      </Box>

      <Footer />
    </div>
  );
}
