import React, { useState } from "react";
import Appbar from "./components/Appbar";
import Footer from "./components/Footer";
import img1 from "./images/hotel1.jpg";
import img2 from "./images/hotel2.jpg";
import img3 from "./images/hotel3.jpg";
import img4 from "./images/hotel4.jpg";
import img5 from "./images/hotel5.jpg";
import img6 from "./images/hotel6.jpg";
import img7 from "./images/hotel7.jpg";
import img8 from "./images/hotel8.jpg";
import img9 from "./images/hotel9.jpg";
import img10 from "./images/hotel10.jpg";
import img11 from "./images/hotel11.jpg";

import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardMedia,
  Modal,
  Backdrop,
  Fade,
} from "@mui/material";

export default function Hotel() {
    const images = [
        { id: 1, src: img1, alt: "Hotel image 1" },
        { id: 2, src: img2, alt: "Hotel image 2" },
        { id: 3, src: img3, alt: "Hotel image 3" },
        { id: 4, src: img4, alt: "Hotel image 4" },
        { id: 5, src: img5, alt: "Hotel image 5" },
        { id: 6, src: img6, alt: "Hotel image 6" },
        { id: 7, src: img7, alt: "Hotel image 7" },
        { id: 8, src: img8, alt: "Hotel image 8" },
        { id: 9, src: img9, alt: "Hotel image 9" },
        { id: 10, src: img10, alt: "Hotel image 10" },
        { id: 11, src: img11, alt: "Hotel image 11" },
      ];
    
      const [open, setOpen] = useState(false);
      const [currentImage, setCurrentImage] = useState(null);
    
      const handleOpen = (image) => {
        setCurrentImage(image);
        setOpen(true);
      };
    
      const handleClose = () => {
        setOpen(false);
      };
    
      return (
        <div>
          <Appbar />
          <Box sx={{ textAlign: "center", mt: 4 }}>
            <Typography sx={{ fontFamily: "poppins" }} variant="h5">
              Hotel
            </Typography>
            <Typography variant="h6" sx={{ fontFamily: "poppins", mt: 2 }}>
            Escape to unparalleled hospitality and luxury, where every stay promises a memorable experience
            </Typography>
          </Box>
          <Container sx={{ mt: 4 }}>
            <Grid container spacing={4} justifyContent="center">
              {images.map((image) => (
                <Grid item xs={12} sm={6} md={4} key={image.id}>
                  <Card onClick={() => handleOpen(image)} style={{borderRadius:"10px"}} elevation={6}>
                    <CardMedia
                      component="img"
                      src={image.src}
                      alt={image.alt}
                      style={{
                        objectFit: "cover",
                        width: "100%",
                        height: "250px",
                        cursor: "pointer",
                      }}
                      className="img-hover"
                    />
                  </Card>
                </Grid>
              ))}
            </Grid>
            <Modal
              open={open}
              onClose={handleClose}
              aria-labelledby="modal-modal-title"
              aria-describedby="modal-modal-description"
              closeAfterTransition
              BackdropComponent={Backdrop}
              BackdropProps={{
                timeout: 500,
              }}
            >
              <Fade in={open}>
                <Box
                  sx={{
                    position: "absolute",
                    top: "50%",
                    left: "50%",
                    transform: "translate(-50%, -50%)",
                    maxWidth: "100vw",
                    maxHeight: "100vh",
                    overflow: "hidden",
                    bgcolor: "background.paper",
                    boxShadow: 24,
                    outline: "none",
                  }}
                >
                  <img
                    src={currentImage ? currentImage.src : ""}
                    alt={currentImage ? currentImage.alt : ""}
                    style={{
                      objectFit: "contain",
                      width: "100%",
                      height: "100%",
                    }}
                  />
                </Box>
              </Fade>
            </Modal>
          </Container>
    
          <Footer />
        </div>
      );
}
