import React, { useState } from "react";
import Appbar from "./components/Appbar";
import Footer from "./components/Footer";
import img1 from "./images/img01.jpg";
import img2 from "./images/img05.jpg";
import img3 from "./images/img06.jpg";
import img4 from "./images/img07.jpg";
import img04 from "./images/img11.jpg";
import img5 from "./images/res5.jpg";

import img7 from "./images/res7.jpg";
import img8 from "./images/res8.jpg";
import img9 from "./images/res9.jpg";
import img10 from "./images/res10.jpg";
import img11 from "./images/res11.jpg";
import img12 from "./images/res12.jpg";
import img13 from "./images/res13.jpg";

import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardMedia,
  Modal,
  Backdrop,
  Fade,
} from "@mui/material";

export default function Shop() {
  const cardStyle = { borderRadius: "10px" };
  const images = [
    { id: 1, src: img1, alt: "Shop image 1" },
    { id: 2, src: img2, alt: "Shop image 2" },
    { id: 3, src: img3, alt: "Shop image 3" },
    { id: 4, src: img4, alt: "Shop image 4" },
    { id: 12, src: img04, alt: "Shop image 4" },
    { id: 13, src: img12, alt: "Shop image 12" },
    { id: 14, src: img13, alt: "Shop image 13" },
    { id: 20, src: img5, alt: "Shop image 5" },

    { id: 7, src: img7, alt: "Shop image 7" },
    { id: 8, src: img8, alt: "Shop image 8" },
    { id: 9, src: img9, alt: "Shop image 9" },
    { id: 10, src: img10, alt: "Shop image 10" },
    { id: 11, src: img11, alt: "Shop image 11" },
  ];

  const [open, setOpen] = useState(false);
  const [currentImage, setCurrentImage] = useState(null);

  const handleOpen = (image) => {
    setCurrentImage(image);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Appbar />
      <Box sx={{ textAlign: "center", mt: 4 }}>
        <Typography sx={{ fontFamily: "poppins" }} variant="h5">
          Restaurant
        </Typography>
        <Typography variant="h6" sx={{ fontFamily: "poppins", mt: 2 }}>
          Dive into the world of culinary delights with my restaurant projects,
          where ambiance meets exquisite taste. Each design is tailored to
          create a unique dining experience, blending aesthetics with
          functionality
        </Typography>
      </Box>
      <Container sx={{ mt: 4 }}>
        <Grid container spacing={4} justifyContent="center">
          {images.map((image) => (
            <Grid item xs={12} sm={6} md={4} key={image.id}>
              <Card
                onClick={() => handleOpen(image)}
                style={cardStyle}
                elevation={6}
              >
                <CardMedia
                  component="img"
                  src={image.src}
                  alt={image.alt}
                  style={{
                    objectFit: "cover",
                    width: "100%",
                    height: "250px",
                    cursor: "pointer",
                  }}
                  className="img-hover"
                />
              </Card>
            </Grid>
          ))}
        </Grid>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500,
          }}
        >
          <Fade in={open}>
            <Box
              sx={{
                position: "absolute",
                top: "50%",
                left: "50%",
                transform: "translate(-50%, -50%)",
                maxWidth: "100vw",
                maxHeight: "100vh",
                overflow: "hidden",
                bgcolor: "background.paper",
                boxShadow: 24,
                outline: "none",
              }}
            >
              <img
                src={currentImage ? currentImage.src : ""}
                alt={currentImage ? currentImage.alt : ""}
                style={{
                  objectFit: "contain",
                  width: "100%",
                  height: "100%",
                }}
              />
            </Box>
          </Fade>
        </Modal>
      </Container>

      <Footer />
    </div>
  );
}
